@import url("https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;600;700&family=Source+Sans+Pro:ital,wght@0,400;0,600;1,400&display=swap");
/* 
font-family: 'Open Sans', sans-serif;
font-family: 'Source Sans Pro', sans-serif; 
*/
.container {
  padding: 0;
}

body {
  margin: 0 auto;
}

ul {
  list-style: none;
}

h1, h2, h3, h4, h5 {
  opacity: 0.7;
  font-family: "Open Sans", sans-serif;
  font-size: 37.8px;
  line-height: 34px;
  font-weight: 600;
  color: #000000;
}

p, a, a:hover, a:active {
  opacity: 0.65;
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  line-height: 25px;
  font-weight: 400;
  color: #000000;
  text-decoration: none;
}

input[type="text"], input[type="text"]:active, input[type="text"]:focus,
textarea, textarea:active, textarea:focus {
  border: none;
  background-color: transparent;
  border-bottom: 1px solid #000000;
  outline: none;
  width: 100%;
  opacity: 0.6;
  font-family: "Open Sans", sans-serif;
  font-size: 15px;
  line-height: 25px;
  font-weight: 400;
  color: #000000;
}

input[type=submit] {
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  line-height: 25px;
  font-weight: 400;
  color: #ffffff;
}

.colorText {
  color: #fc6744;
}

.btn {
  width: 228px;
  height: 55px;
  background-color: #ffffff;
  box-shadow: 1px 6px 14px 0px rgba(19, 19, 21, 0.09);
  border-radius: 27px;
}

.btn img {
  width: 18px;
  margin-right: 10px;
}

/* Header */
.header {
  height: 920px;
  background-color: #f5f5f5;
  border-radius: 1px;
}

/* end Header */
/* header__top */
.header__top {
  padding-top: 82px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

/* Logo */
.logo {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 149px;
  height: 55px;
  opacity: 0.6;
  border: 3.6px solid #170f1c;
  border-radius: 2px;
}

/* end Logo */
/* Burger Menu */
.menu {
  display: flex;
  flex-direction: column;
  background-color: transparent;
  align-items: flex-end;
  position: relative;
  height: auto;
  width: auto;
}

.menu div {
  display: flex;
  height: 20px;
  width: 34px;
  flex-direction: column;
  justify-content: space-between;
  margin: 0;
  cursor: pointer;
}

.menu div span {
  display: flex;
  width: 100%;
  height: 4px;
  opacity: 0.6;
  background-color: #170f1c;
  border-radius: 1.5px;
  transition: ease-in-out .5s;
}

.menu div span.active:nth-child(1) {
  transform: rotate(-28deg);
  transition: ease-in-out .5s;
  transform-origin: 100%;
  opacity: 1;
}

.menu div span.active:nth-child(2) {
  opacity: 0;
  transition: ease-in-out .5s;
  transform-origin: 50% 50%;
}

.menu div span.active:nth-child(3) {
  transform: rotate(28deg);
  transition: ease-in-out .5s;
  transform-origin: 100%;
  opacity: 1;
}

.menu nav {
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
  border-radius: 3%;
  transition: ease-in-out 1.5s;
  overflow: hidden;
  position: relative;
  height: 150px;
  width: 150px;
  background-color: #f5f5f5;
  transition: ease-in-out .5s;
}

.menu nav ul {
  position: absolute;
  transition: cubic-bezier(0.6, -0.28, 0.735, 0.045) 0.5s;
  right: -100%;
  opacity: 0;
}

.menu nav .open {
  transition: cubic-bezier(0.175, 0.885, 0.32, 1.275) 0.5s;
  right: 0%;
  opacity: 1;
}

/* end Burger Menu */
/* end header__top */
/* header__middle */
.header__middle {
  margin: 0 auto;
  margin-top: 25px;
}

.header__info {
  text-align: center;
}

.header__info h1 {
  opacity: 0.94;
  font-size: 50px;
  line-height: 45px;
  font-weight: 600;
  color: #626262;
  margin-bottom: 30px;
}

.header__info h1 span {
  color: #fc6744;
}

/* end header__middle */
/* header__bottom */
.header__bottom {
  margin-top: 110px;
}

.message {
  display: flex;
  flex-direction: column;
  width: 372px;
  height: 388px;
  background-color: colorBgFirst;
  box-shadow: 0px 0px 57px 0px rgba(19, 19, 21, 0.2);
  border-radius: 2px;
  padding: 20px 39px 19px;
  justify-content: space-between;
}

.message__text {
  opacity: 0.9;
  font-family: "Open Sans", sans-serif;
  font-size: 22px;
  line-height: 25px;
  font-weight: 400;
  color: #211a18;
  text-align: center;
  margin-bottom: 50px;
}

.message__text span {
  font-weight: 600;
  color: #fc6744;
}

.sendMessage {
  display: flex;
  height: 100%;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.sendMessage__Name {
  height: 50px;
  margin-bottom: 35px;
}

.sendMessage__email {
  height: 50px;
  margin-bottom: 25px;
}

.sendMessage__message {
  height: 100%;
  resize: none;
}

.sendMessage__button {
  width: 240px;
  min-height: 57px;
  background-color: #ff5c36;
  border: 1px solid rgba(1, 2, 2, 0.06);
  box-shadow: 2px 11px 20px -1px rgba(19, 19, 21, 0.07);
  border-radius: 27px;
  margin-top: 20px;
}

/* end header__bottom */
/* body */
.block {
  padding-top: 144px;
  padding-bottom: 61px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.block .container {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.info__title {
  opacity: 0.8;
  font-family: "Open Sans", sans-serif;
  font-size: 50px;
  line-height: 45px;
  font-weight: 600;
  margin-bottom: 9px;
}

.info__title span {
  color: #fc6744;
}

.block-light {
  background-color: #ffffff;
}

.block-dark {
  background-color: #fbfbfb;
}

.workingProcess {
  display: flex;
}

.workingProcess__cards {
  display: flex;
  flex-direction: row;
  margin-top: 90px;
  margin-bottom: 100px;
  justify-content: space-between;
  flex-wrap: wrap;
  width: 100%;
}

.workingProcess__cards .card:nth-child(1) {
  background-color: #4eb2f7;
}

.workingProcess__cards .card:nth-child(2) {
  background-color: #c049f8;
}

.workingProcess__cards .card:nth-child(3) {
  background-color: #817eff;
}

.workingProcess__cards .card:nth-child(4) {
  background-color: #f74770;
}

.workingProcess__cards .card {
  width: 262px;
  height: 263px;
  box-shadow: 0px 0px 35px 0px rgba(19, 19, 21, 0.2);
  align-items: center;
}

.workingProcess__cards .card__image {
  margin-top: 50px;
  max-width: 100%;
  height: auto;
}

.workingProcess__cards .card__title {
  margin-top: 10px;
  opacity: 0.9;
  font-family: "Open Sans", sans-serif;
  font-size: 20px;
  line-height: 42px;
  font-weight: 600;
  letter-spacing: 0.01em;
  color: #ffffff;
}

.workingProcess__cards .card__info {
  margin-top: 10px;
  opacity: 0.74;
  font-family: "Open Sans", sans-serif;
  font-size: 15px;
  line-height: 22px;
  font-weight: 400;
  letter-spacing: 0.01em;
  color: #ffffff;
  width: 200px;
  text-align: center;
}

.workingProcess__cards .card__btn {
  position: absolute;
  bottom: -25px;
}

.featuredWorks__images {
  margin-bottom: 85px;
}

.featuredWorks__images .workImages {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  margin-top: 100px;
}

.featuredWorks__images .workImages .image {
  width: 360px;
  height: 358px;
  background-color: #ff5c36;
  margin-bottom: 23px;
}

.ServicesWeProvide__info {
  margin-bottom: 110px;
}

.options {
  display: flex;
}

.options__choiser .choose {
  display: flex;
  width: 104px;
  height: 92px;
  background-color: #ffffff;
  box-shadow: 0px 0px 36px 0px rgba(19, 19, 21, 0.2);
  border-radius: 2px;
  margin: 7px 0px;
  justify-content: center;
  align-items: center;
}

.options__choiser .choose img {
  padding: 25px 0px 0px 15px;
  margin: 0 auto;
}

.options__information {
  display: flex;
  flex-direction: column;
  text-align: left;
  padding-left: 50px;
}

.options__information p {
  margin-bottom: 20px;
}

.options__information .title {
  opacity: 0.74;
  font-family: "Open Sans", sans-serif;
  font-size: 30px;
  line-height: 45px;
  font-weight: 400;
  color: #000000;
  position: relative;
  margin-bottom: 25px;
}

.options__information .title::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: -10px;
  border-style: solid;
  border-color: #ff5c36;
  border-width: 2px;
  width: 57px;
}

.options__image {
  margin-top: 15px;
  padding-left: 55px;
}

.ServicesWeProvide__options {
  margin-bottom: 100px;
}

.team__cards {
  display: flex;
  flex-wrap: wrap;
  margin-top: 107px;
  margin-bottom: 105px;
}

.team__cards .box {
  width: 360px;
  height: 445px;
  box-shadow: 0px 0px 2px 0px rgba(19, 19, 21, 0.6);
  position: relative;
  margin: 0 15px;
  background-color: #fff3f0;
  z-index: 0;
}

.team__cards .box__hide_color {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 10;
  background-color: #ff5c36;
}

.team__cards .box__hide_color:hover {
  opacity: 0;
}

.team__cards .box__skills_ui {
  width: 151px;
  height: 4px;
  background-color: #817f7e;
  border-radius: 1.5px;
}

.team__cards .box__skills_ui::after {
  content: none;
  background-color: #ff6e47;
  border-radius: 1.5px;
}

.team__cards .box:nth-child(1)__skills_ui:nth-child(1)::after {
  content: "";
  background-color: #ff6e47;
  border-radius: 1.5px;
  display: block;
  height: 100%;
  width: 70%;
  border: 2px solid #ff5c36;
}

.team__cards .box:first-child {
  margin-left: 0;
}

.team__cards .box:last-child {
  margin-right: 0;
}
